@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto/Roboto-Regular.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto/Roboto-Bold.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Rosario";
  font-style: normal;
  font-weight: 799;
  src: url("../fonts/rosario/Rosario-Bold.ttf") format("truetype");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
  transition: color 0.2s ease-in-out;
}
a:hover {
  color: #e11c4b;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

:root {
  --h1: 36px;
  --h2: 22px;
  --text: 18px;
  --small-text: 16px;
  --smallest-text: 14px;
  --mobile-header-text: 10px;
}
@media (max-width: 1229px) {
  :root {
    --h2: 18px;
  }
}
@media (max-width: 767px) {
  :root {
    --h1: 24px;
    --h2: 17px;
    --text: 16px;
    --small-text: 14px;
    --smallest-text: 12px;
  }
}

.page {
  height: 100%;
}

.page__body {
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-size: var(--text);
  font-weight: 400;
  color: #333333;
  font-family: "Roboto", "Tahoma", sans-serif;
}
@media (max-width: 767px) {
  .page__body {
    padding-bottom: 64px;
  }
}
.page__body main {
  flex: 1;
}

.container {
  max-width: 1229px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (max-width: 1229px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 10px;
  }
}

.contact-link {
  padding: 8px 0 8px 0;
  margin: 0;
  color: #333333;
  font-size: var(--text);
  transition: color 0.2s ease-in-out;
  display: flex;
  gap: 6px;
  align-items: center;
}
.contact-link:hover .contact-link__icon {
  color: #e11c4b;
}
.contact-link__icon {
  color: #8d8d8d;
  transition: color 0.2s ease-in-out;
}

.button-toggle {
  position: relative;
  border: 0;
  cursor: pointer;
  background-color: transparent;
  width: 24px;
  height: 17px;
  display: flex;
  align-items: center;
  padding: 0;
  transition: all 0.2s ease-in-out;
}
.button-toggle:hover::before, .button-toggle:hover::after,
.button-toggle:hover .button-toggle__line {
  background-color: #e11c4b;
}
.button-toggle::before, .button-toggle::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  transition: all 0.3s ease 0s;
  background-color: #8d8d8d;
}
.button-toggle::before {
  top: 0;
}
.button-toggle::after {
  bottom: 0;
}
.button-toggle--active {
  opacity: 0.6;
}
.button-toggle--active .button-toggle__line {
  transform: scale(0);
}
.button-toggle--active::before {
  top: 50%;
  transform: rotate(-45deg) translate(0, -50%);
}
.button-toggle--active::after {
  bottom: 50%;
  transform: rotate(45deg) translate(0, 50%);
}

.button-toggle__line {
  display: block;
  background-color: #8d8d8d;
  width: 24px;
  height: 2px;
  transform: scale(1);
}

.main-header__wrapper {
  display: flex;
  gap: 20px 0;
  flex-wrap: wrap;
  padding-top: 20px;
  align-items: center;
}
@media (max-width: 1229px) {
  .main-header__wrapper {
    padding-top: 16px;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    padding-top: 20px;
    gap: 12px 0;
  }
}

@media (max-width: 767px) {
  .main-header__button {
    margin-right: 15px;
    margin-left: auto;
  }
}

.main-header__logo {
  font-size: var(--h2);
  font-family: "Rosario", "Tahoma", sans-serif;
  color: #e11c4b;
  transition: opacity 0.2s ease-in-out;
  font-weight: 700;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  padding: 9px 51px 10px 0;
}
.main-header__logo:hover {
  opacity: 0.8;
}
@media (max-width: 1229px) {
  .main-header__logo {
    padding: 11px 45px 13px 0;
  }
}
@media (max-width: 767px) {
  .main-header__logo {
    padding: 12px 15px 12px 0;
    width: 50%;
    order: -1;
  }
}

.main-header__input-wrapper {
  position: relative;
  flex-grow: 1;
  min-width: 300px;
}
@media (max-width: 1229px) {
  .main-header__input-wrapper {
    min-width: initial;
  }
}
.main-header__input-wrapper .main-header__input-icon {
  position: absolute;
  color: #e11c4b;
  left: 18px;
  top: 50%;
  transform: translateY(-50%);
}

.main-header__input-search {
  width: 100%;
  border-radius: 100px;
  border: 2px solid #dddddd;
  padding: 13px 13px 13px 50px;
  font-size: var(--small-text);
  color: #333333;
  transition: background-color 0.2s ease-in-out;
}
.main-header__input-search::placeholder {
  font-size: var(--small-text);
  color: #8d8d8d;
}
.main-header__input-search:hover {
  background-color: #f4f4f4;
}

.main-header__nav {
  margin-left: auto;
}
@media (max-width: 767px) {
  .main-header__nav {
    order: -1;
    width: 50%;
  }
}

.main-header__nav-list {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  margin-left: 25px;
  justify-content: flex-end;
}
@media (max-width: 1229px) {
  .main-header__nav-list {
    gap: 12px 30px;
    margin-left: 35px;
  }
}
@media (max-width: 767px) {
  .main-header__nav-list {
    gap: 0;
    justify-content: flex-end;
    margin-left: 0;
  }
}

.main-header__nav-item--mobile {
  display: none;
}
@media (max-width: 767px) {
  .main-header__nav-item--mobile {
    display: block;
  }
}
@media (max-width: 767px) {
  .main-header__nav-item--desktop {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-header__nav-item:last-child .main-header__nav-link {
    padding-right: 0;
  }
}

.main-header__nav-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: inherit;
  gap: 2px;
}
.main-header__nav-link:hover svg {
  color: inherit;
}
@media (max-width: 767px) {
  .main-header__nav-link {
    padding: 12px;
  }
}
.main-header__nav-link svg {
  color: #8d8d8d;
  transition: color 0.2s ease-in-out;
}
@media (max-width: 1229px) {
  .main-header__nav-link svg {
    width: 24px;
    height: 24px;
  }
}

.main-header__nav-link-text {
  font-size: var(--small-text);
}
@media (max-width: 1229px) {
  .main-header__nav-link-text {
    display: none;
  }
}

.main-header__contact {
  border-bottom: 2px solid #e8e8e8;
}
@media (max-width: 767px) {
  .main-header__contact {
    display: none;
  }
}
.main-header__contact-inner {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 8px 36px;
}
.main-header__contact-phone {
  margin-left: auto;
}

@media (max-width: 1229px) {
  .main-header__categories .container {
    padding-right: 0;
  }
}

.main-header__categories-wrapper {
  position: relative;
}

.main-header__categories-list {
  overflow-x: auto;
  max-width: 100%;
  white-space: nowrap;
  display: flex;
}
.main-header__categories-list::-webkit-scrollbar {
  height: 4px;
}
.main-header__categories-list::-webkit-scrollbar-thumb {
  background-color: #e8e8e8;
  border-radius: 4px;
}
.main-header__categories-list::-webkit-scrollbar-track {
  background-color: transparent;
}

.main-header__categories-link {
  padding: 20px 12px 20px 24px;
  transition: color 0.2s ease-in-out;
  white-space: nowrap;
  display: flex;
  align-items: center;
  gap: 6px;
}
@media (max-width: 1229px) {
  .main-header__categories-link {
    padding: 18px 18px 20px 18px;
  }
}
@media (max-width: 767px) {
  .main-header__categories-link {
    padding: 18px 9px 18px 9px;
  }
}
.main-header__categories-link svg {
  color: #8d8d8d;
  transition: color 0.2s ease-in-out;
}
.main-header__categories-link--active {
  color: #e11c4b;
}
.main-header__categories-link--active svg {
  color: #e11c4b;
}
.main-header__categories-link:hover {
  color: #e11c4b;
}
.main-header__categories-link:hover svg {
  color: #e11c4b;
}

.main-header__categories-item:first-child .main-header__categories-link {
  padding-left: 0;
}

.main-header__categories-item:last-child .main-header__categories-link {
  padding-right: 30px;
}
@media (max-width: 1229px) {
  .main-header__categories-item:last-child .main-header__categories-link {
    padding-right: 18px;
  }
}
@media (max-width: 767px) {
  .main-header__categories-item:last-child .main-header__categories-link {
    padding: 9px;
  }
}

.main-header__categories-button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  display: flex;
  color: #8d8d8d;
  transition: color 0.2s ease-in-out;
  cursor: pointer;
}
.main-header__categories-button:hover {
  color: #e11c4b;
}
@media (max-width: 1229px) {
  .main-header__categories-button {
    display: none;
  }
}

.main-header-mobile {
  display: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #ffffff;
  box-shadow: 0 -3px 13px 0 rgba(146, 146, 146, 0.25);
  z-index: 2;
}
@media (max-width: 767px) {
  .main-header-mobile {
    display: block;
  }
}

.main-header-mobile__nav {
  padding-top: 13px;
  padding-bottom: 11px;
}

.main-header-mobile__nav-list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 8px;
  justify-items: center;
  align-items: center;
}

.main-header-mobile__nav-item {
  position: relative;
  width: 100%;
}
.main-header-mobile__nav-item:hover svg {
  color: #e11c4b;
}
.main-header-mobile__nav-item svg {
  color: #8d8d8d;
  transition: color 0.2s ease-in-out;
}
.main-header-mobile__nav-item--active, .main-header-mobile__nav-item--active svg {
  color: #e11c4b;
}

.main-header-mobile__nav-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: inherit;
  gap: 4px;
}

.main-header-mobile__nav-text {
  font-size: var(--mobile-header-text);
}

.catalog-menu-mobile {
  position: fixed;
  padding: 18px 0;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow-y: scroll;
  z-index: 3;
  background-color: #ffffff;
  transition: right 0.5s ease-in-out;
}
.catalog-menu-mobile--closed {
  right: 100%;
}
.catalog-menu-mobile--open {
  right: 0;
}

.catalog-menu-mobile__button-close {
  margin-left: auto;
}

.catalog-menu-mobile__button-open {
  display: none;
}
@media (max-width: 767px) {
