  position: absolute;
  content: "";
  width: 8px;
  height: 15px;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  mask-image: url("../img/icons/stack.svg#catalog-arrow");
  background-color: #8d8d8d;
