.main-footer {
  padding: 24px 0 18px;
  margin-bottom: 53px;
  background-color: #f4f4f4;
  color: #333333;
  font-size: var(--small-text);
}
@media (max-width: 1229px) {
  .main-footer {
    margin-bottom: 15px;
  }
}
@media (max-width: 767px) {
  .main-footer {
    padding: 12px 0 24px;
    margin-bottom: 0;
  }
}

.main-footer__inner {
  display: grid;
  grid-template-columns: 400px 1fr;
  align-items: center;
  grid-auto-flow: dense;
  gap: 24px;
}
@media (max-width: 1229px) {
  .main-footer__inner {
    grid-template-columns: auto 1fr;
    gap: 0 12px;
  }
}
@media (max-width: 767px) {
  .main-footer__inner {
    display: block;
  }
}

.main-footer__nav {
  grid-column: 1/2;
}

.main-footer__nav-list {
  display: flex;
  column-gap: 14px;
  flex-wrap: wrap;
}
@media (max-width: 1229px) {
  .main-footer__nav-list {
    gap: 0;
  }
}

.main-footer__nav-item:first-child .main-footer__nav-link {
  padding-left: 0;
}
.main-footer__nav-item:last-child .main-footer__nav-link {
  padding-right: 0;
}

.main-footer__nav-link {
  padding: 12px;
}
@media (max-width: 767px) {
  .main-footer__nav-link {
    padding-bottom: 8px;
  }
}

.main-footer__contact-list {
  grid-column: 2/3;
  display: flex;
  gap: 0 62px;
  flex-wrap: wrap;
  justify-content: flex-end;
}
@media (max-width: 1229px) {
  .main-footer__contact-list {
    grid-column: 1/2;
    justify-content: initial;
    gap: 0 24px;
  }
}
@media (max-width: 767px) {
  .main-footer__contact-list {
    flex-direction: column;
  }
}

.main-footer__contact-link {
  gap: 10px;
  padding: 12px 0 12px 0;
  font-size: var(--small-text);
}
@media (max-width: 767px) {
  .main-footer__contact-link {
    padding: 4px 0 4px 0;
  }
  .main-footer__contact-link svg {
    width: 18px;
    height: 18px;
  }
}
@media (max-width: 767px) {
  .main-footer__contact-link--mail svg {
    width: 18px;
    height: 11px;
  }
}

.main-footer__social-list {
  grid-column: 2/3;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 12px;
}
@media (max-width: 1229px) {
  .main-footer__social-list {
    grid-row: 1/4;
    align-self: start;
    margin-top: 18px;
  }
}
@media (max-width: 767px) {
  .main-footer__social-list {
    justify-content: initial;
    margin-top: 8px;
  }
}

.main-footer__social-link {
  border-radius: 50%;
  border: 1px solid #8d8d8d;
  padding: 8px;
  display: flex;
  margin: auto;
  color: #8d8d8d;
}
.main-footer__social-link:hover {
  border-color: #e11c4b;
}
@media (max-width: 767px) {
  .main-footer__social-link {
    padding: 6px;
  }
}
.main-footer__social-link svg {
  display: flex;
}
@media (max-width: 767px) {
  .main-footer__social-link svg {
    width: 18px;
    height: 18px;
  }
}

.main-footer__copyright {
  grid-column: 1/2;
  font-size: var(--smallest-text);
  color: #939393;
  margin: 0;
}
@media (max-width: 1229px) {
  .main-footer__copyright {
    margin-top: 12px;
  }
}
@media (max-width: 767px) {
  .main-footer__copyright {
    margin-top: 24px;
  }
}

.catalog {
  padding-top: 16px;
  padding-bottom: 96px;
}
@media (max-width: 1229px) {
  .catalog {
    padding-top: 18px;
    padding-bottom: 72px;
  }
}
@media (max-width: 767px) {
  .catalog {
    padding-top: 7px;
    padding-bottom: 48px;
  }
}

.catalog__title {
  margin: 0 0 36px;
}
@media (max-width: 767px) {
  .catalog__title {
    margin-bottom: 24px;
  }
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 34px;
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 1229px) {
  .catalog__list {
    gap: 18px;
  }
}

.catalog__item {
  box-shadow: 0 6px 30px 4px rgba(32, 32, 32, 0.1);
  border-radius: 10px;
  transition: transform 0.5s ease-in-out;
}
.catalog__item img {
  width: 100%;
  height: 100%;
}
.catalog__item:hover {
  transform: scale(1.01);
}

.product {
  padding: 16px 0 82px;
}
@media (max-width: 1229px) {
  .product {
    padding: 18px 0 60px;
  }
}
@media (max-width: 767px) {
  .product {
    padding: 7px 0 36px;
  }
}

.product__img {
  width: 100%;
  height: auto;
  margin-bottom: 36px;
  box-shadow: 0 6px 30px 4px rgba(32, 32, 32, 0.1);
  border-radius: 30px;
}
@media (max-width: 1229px) {
  .product__img {
    border-radius: 20px;
  }
}
@media (max-width: 767px) {
  .product__img {
    margin-bottom: 24px;
    border-radius: 10px;
  }
}

.product__title {
  margin: 0 0 18px;
  font-size: var(--h1);
  font-weight: 700;
}
@media (max-width: 767px) {
  .product__title {
    margin-bottom: 12px;
  }
}

.product__date {
  margin: 0 0 24px;
  font-size: var(--h2);
  color: #939393;
}

.product__description {
  margin: 0 0 14px;
  font-size: var(--text);
}
@media (max-width: 1229px) {
  .product__description {
    margin-bottom: 12px;
  }
}